// react States
// import { useEffect, useState } from 'react'
import React, { Component } from 'react';
import { Link } from 'react-router-dom'
// import { Link, Route, Switch } from 'react-router-dom'
// import { useNavigate } from "react-router-dom";

// Images
import fairtraderLogo from '../Images/fairtraderLogo.png';
import searchBtn from '../Images/searchBtn.png';
import toggleBtn from '../Images/toggleBtn.png';
import invoice from '../Images/activeInvoice.png';
import resolution from '../Images/resolution.png';
import contract from '../Images/contract.png';
import myWallet from '../Images/myWallet.png';
import invoiceAdd from '../Images/Invoice/invoiceAdd.png';
import invoiceAttachDarkArrow from '../Images/Invoice/invoiceAttachDarkArrow.png';
import invoiceAttachHR from '../Images/Invoice/invoiceAttachHR.png';
import invoiceAttachLightArrow from '../Images/Invoice/invoiceAttachLightArrow.png';
import invoiceCalender from '../Images/Invoice/invoiceCalender.png';
import invoiceDelete from '../Images/Invoice/invoiceDelete.png';
import invoiceEmail from '../Images/Invoice/invoiceEmail.png';
import invoiceLink from '../Images/Invoice/invoiceLink.png';
import invoiceMap from '../Images/Invoice/invoiceMap.png';
import invoiceNextResolution from '../Images/Invoice/invoiceNextResolution.png';
import invoicePaymentArrow from '../Images/Invoice/invoicePaymentArrow.png';
import invoicePaymentGreenArow from '../Images/Invoice/invoicePaymentGreenArow.png';
import invoicePaymentWightArow from '../Images/Invoice/invoicePaymentWightArow.png';
import invoicePDF from '../Images/Invoice/invoicePDF.png';
import invoiceProfile from '../Images/Invoice/invoiceProfile.png';
import invoiceSetting from '../Images/Invoice/invoiceSetting.png';
import invoiceWallet from '../Images/Invoice/invoiceWallet.png';
import invoiceName from '../Images/Invoice/invoiceName.png';
import invoiceDummyPDF from '../Images/Invoice/invoiceDummyPDF.png';
import invoiceDummyPDF2 from '../Images/Invoice/invoiceDummyPDF2.pdf';

// Toast
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css'

// Calender
import Calendar from 'react-calendar';
import 'react-calendar/dist/Calendar.css';



// components
// import HeaderNav from '../components/HeaderNav.jsx';


// css
import './css/Invoice.css'
import './css/invoiceCalender.css'
// import axios from 'axios';
class App extends Component {

    constructor(props) {
        super(props)
        this.state = {
            handleAddInvoice: false,
            attachInvoiceFileName: '',
            paymentCalender: false,
            calenderValue: '',
            formatedCalenderValue: '',
            viewPDF: false
        }
    }
    async componentWillMount() { }

    handleSelectDate() {
        var dateObj = this.state.calenderValue;
        if (dateObj !== '') {
            var month = dateObj.toLocaleString('default', { month: 'short' })
            var day = dateObj.getDate();
            var year = dateObj.getFullYear();

            var newdate = day + " " + month + " " + year;
            console.log(newdate);
            this.setState({ formatedCalenderValue: newdate })

            this.setState({ paymentCalender: false })
        } else {
            toast.error("First, Select the Date", {
                position: 'top-right'
            })
        }
    }

    invoiceAttachName() {
        var name = document.getElementById('invoiceFIleAttach');
        if (name.files.item(0).size > '250000') {
            console.log('large');
            toast.error("Invoice must be smaller than 250.0 KB", {
                position: 'top-right'
            })
        } else {
            this.setState({ attachInvoiceFileName: name.files.item(0).name })
            // alert('Selected file: ' + name.files.item(0).size);
            // alert('Selected file: ' + name.files.item(0).type);
        }
    };

    handleViewPDF() {
        this.setState({ viewPDF: true })
        document.getElementById('invoiceBlackContainer').style.display = 'none';
    }
    handleClossPDF() {
        this.setState({ viewPDF: false })
        document.getElementById('invoiceBlackContainer').style.display = 'inherit';
    }

    render() {

        let attachedInvoice;
        if (this.state.attachInvoiceFileName === '') {
            attachedInvoice = <div>
                <span className="alignStart">
                    <span style={{ fontWeight: 'normal', marginLeft: '6px', fontSize: '17px' }}><b>Add Invoice</b></span>
                </span>
                <span className="alignEnd" style={{ float: 'right', marginRight: '-8px' }}>
                    <img src={invoiceAdd} style={{ marginTop: '-1px' }} className='floatRight' alt="walletGreaterSign" />
                </span>
            </div>
        } else {
            attachedInvoice = <div>
                <span className="alignStart">
                    <span style={{ fontWeight: 'normal', marginLeft: '4px', fontSize: '16px' }}><b>Update Invoice</b></span>
                </span>
                <span className="alignEnd" style={{ float: 'right', marginRight: '-8px' }}>
                    <img src={invoicePDF} style={{ marginTop: '-1px' }} className='floatRight' alt="walletGreaterSign" />
                </span>
            </div>
        }

        let invoiceBtn;
        if (this.state.paymentCalender === true) {
            invoiceBtn = ''
        } else if (this.state.formatedCalenderValue !== '' && this.state.attachInvoiceFileName !== '') {
            invoiceBtn =
            <div className='selectResolutionDIv invoiceThreeBtnDiv'>
                <span className="alignStart">
                    <img
                        src={invoiceDelete}
                        alt="mapIcon"
                    />
                </span>
                <span className="invoiceThreeBtn">
                    <p className="selectResolutionBtn alignCenter" onClick={() => { this.handleViewPDF() }} style={{ width: '200px' }} >
                        View PDF
                    </p>
                </span>
                <span className="alignEnd" style={{ float: 'right' }}>
                    <Link to={{ pathname: '/Resolution' }}>
                        <img src={invoiceNextResolution} className='floatRight' alt="walletGreaterSign" />
                    </Link>
                </span>
            </div>
        } else {
            invoiceBtn =
            <div className='selectResolutionDIv'>
                <div className="selectResolutionBtnDiv">
                    <p className="selectResolutionBtn alignCenter" >
                        Enter Information
                    </p>
                </div>
            </div>
        }

        let viewPDF;
        if (this.state.viewPDF === true) {
            viewPDF =
                <div className='viewPDFContainer'>
                    <img src={invoiceDummyPDF} alt="invoiceDummyPDF" />
                    <img src={invoiceDelete} onClick={() => { this.handleClossPDF() }} className='invoicePDFCloss' alt="invoiceDelete" />
                </div>
        }

        let formatedCalenderValueUI;
        if (this.state.formatedCalenderValue === '') {
            formatedCalenderValueUI = "Payment"
        } else {
            formatedCalenderValueUI = this.state.formatedCalenderValue
        }

        let paymentCalender;
        if (this.state.paymentCalender === true) {
            paymentCalender = <div>
                <div className="row paymentCalender">
                    <div className="col-6 paymentCalender1">
                        <div className="row" style={{ marginLeft: '-8px' }}>
                            <div className="col-3 invoiceDarkAdd">
                                <img src={invoicePaymentGreenArow} alt="invoiceAttachDarkArrow" />
                            </div>
                            <div className="col-9">
                                <h6 className='addInvoiceDark' style={{ color: '#21fefe' }}>Auto Release</h6>
                            </div>
                        </div>
                        <center>
                            <img src={invoiceAttachHR} className='invoiceAttachHR' alt="invoiceAttachHR" />
                        </center>
                        <div className="row invoiceLightAdd" style={{ marginLeft: '-8px' }}>
                            <div className="col-3 invoiceDarkAdd">
                                <img src={invoicePaymentWightArow} alt="invoiceAttachLightArrow" />
                            </div>
                            <div className="col-9">
                                <h6 className='addInvoiceDark' style={{ color: '#fff' }} onClick={() => {
                                    this.setState({ formatedCalenderValue: 'On Delivery' })
                                    this.setState({ paymentCalender: false })
                                }}>Upon Delivery</h6>
                            </div>
                        </div>
                        <center>
                            <button className='paymentButton' onClick={() => { this.handleSelectDate() }}>Select Date</button>
                        </center>
                    </div>
                    <div className="col-6 paymentCalender2">
                        <Calendar
                            // activeStartDate={new Date(2017, 0, 1)}
                            formatShortWeekday={(locale, date) => ['S', 'M', 'T', 'W', 'T', 'F', 'S'][date.getDay()]}

                            onChange={(date) => {
                                this.setState({ calenderValue: date })
                                console.log(date);
                            }} />
                    </div>
                </div>
            </div>
        }

        let addInvoice;
        if (this.state.handleAddInvoice === true) {
            addInvoice = <div className="row">
                <div className="col-6">
                </div>
                <div className="col-6">
                    <div className="resolutionOptionstoggle invoiceAdd">
                        <div className="row">
                            <div className="col-3 invoiceDarkAdd">
                                <img src={invoiceAttachDarkArrow} alt="invoiceAttachDarkArrow" />
                            </div>
                            <div className="col-9">
                                <input type="file" accept="application/pdf"
                                    onChange={() => {
                                        this.invoiceAttachName()
                                        this.state.handleAddInvoice = false
                                    }}
                                    style={{ display: 'none' }}
                                    id="invoiceFIleAttach" name="file" />

                                <h6 className='addInvoiceDark' onClick={() => {
                                    document.getElementById('invoiceFIleAttach').click()
                                }}>Attach Invoice</h6>
                            </div>
                        </div>
                        <center>
                            <img src={invoiceAttachHR} className='invoiceAttachHR' alt="invoiceAttachHR" />
                        </center>
                        <div className="row invoiceLightAdd">
                            <div className="col-3 invoiceDarkAdd">
                                <img src={invoiceAttachLightArrow} alt="invoiceAttachLightArrow" />
                            </div>
                            <div className="col-9">
                                <h6 className='addInvoiceDark' style={{ color: '#4D4D4D' }}>Create Invoice</h6>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        }

        let openDropdownContent;
        if (this.state.openDropdown === true && this.state.registeredMediator === true) {
            openDropdownContent = <div className="resolutionOptionstoggle registeredmediator">
                <div className='registeredCompanies'>
                    <p onClick={() => { this.handleSelectIndustry('Construction') }}>Construction</p>
                </div>
            </div>
        } else {
            // this.setState({openDropdown: false})
            this.state.openDropdown = false;
        }


        return (
            <div className="authMainDiv">
                <div className="topNav">
                    <div className="row">
                        <div className="col-6">
                            <img
                                src={fairtraderLogo}
                                className="topLogoPic"
                                alt="fairtraderLogo"
                            />
                        </div>
                        <div className="col-6">
                            <p className="alignEnd">
                                <img
                                    src={searchBtn}
                                    alt="searchBtn"
                                    style={{ marginRight: '20px' }}
                                />
                                <img
                                    src={toggleBtn}
                                    alt="toggleBtn"
                                    style={{ marginRight: '-5px' }}
                                />
                            </p>
                        </div>
                    </div>
                </div>
                <div className="topNavLinks" >
                    <div className="row iconNav">
                        <div className="col-3">
                            <p className="alignCenter activeImg activeImgInvoice">
                                <Link to={{ pathname: '/Invoice' }}>
                                    <img
                                        src={invoice}
                                        className='invoiceImg'
                                        alt="invoice.png" />
                                </Link>
                            </p>
                        </div>
                        <div className="col-3">
                            <p className="alignCenter iconSubNav">
                                <Link to={{ pathname: '/Resolution' }}>
                                    <img
                                        src={resolution}
                                        className='resolutionImg'
                                        style={{ width: '81px', marginTop: '-1px', marginLeft: '-5px' }}
                                        alt="resolution" />
                                </Link>
                            </p>
                        </div>
                        <div className="col-3">
                            <p className="alignCenter iconSubNav">
                                <Link to={{ pathname: '/Contract' }}>
                                    <img
                                        src={contract}
                                        className='contractImg'
                                        alt="contract" />
                                </Link>
                            </p>
                        </div>
                        <div className="col-3">
                            <p className="alignCenter iconSubNav">
                                <Link to={{ pathname: '/Wallet' }}>
                                    <img
                                        src={myWallet}
                                        className='mywalletImg'
                                        alt="wallet" />
                                </Link>
                            </p>
                        </div>
                    </div>
                </div>
                <div className="handleMainPage handleMainPageInvoice">
                    {viewPDF}
                    <div className="invoiceBlackContainer" id='invoiceBlackContainer'>
                        <p className='invoiceFirstLine'>
                            <img src={invoiceProfile} className='alignStart' style={{ float: 'left' }} alt="walletFirstParaIcon" />
                            <span className='invoiceFeildsTxt'>ABC Services</span>
                            <span className='invoiceFirstToken'>#180040</span><img src={invoiceSetting} style={{ marginTop: '2px' }} className='floatRight' alt="walletGreaterSign" />
                        </p>

                        <div className="resolutionOptionstoggle optionChange autoPickedData invoiceFeilds">
                            <span className="alignStart">
                                <img
                                    src={invoiceName}
                                    alt="mapIcon"
                                    style={{ marginTop: '-1px', marginRight: '10px' }}
                                />
                                <input type="invoiceFields" className='mutualFriendInput invoiceFields' placeholder="enter customer name" />
                            </span>
                            <span className="alignEnd" style={{ float: 'right', marginRight: '-8px' }}>
                                <img src={invoiceLink} style={{ marginTop: '0px' }} className='floatRight' alt="walletGreaterSign" />
                            </span>
                        </div>

                        <div className="resolutionOptionstoggle optionChange autoPickedData invoiceFeilds">
                            <span className="alignStart">
                                <img
                                    src={invoiceMap}
                                    alt="mapIcon"
                                    style={{ marginTop: '-1px', marginRight: '11px', marginLeft: '2px' }}
                                />
                                <input type="invoiceFields" className='mutualFriendInput invoiceFields' placeholder="enter customer address" />
                            </span>
                            <span className="alignEnd" style={{ float: 'right', marginRight: '-8px' }}>
                                <img src={invoiceLink} style={{ marginTop: '0px' }} className='floatRight' alt="walletGreaterSign" />
                            </span>
                        </div>

                        <div className="resolutionOptionstoggle optionChange autoPickedData invoiceFeilds">
                            <span className="alignStart">
                                <img
                                    src={invoiceEmail}
                                    alt="mapIcon"
                                    style={{ marginTop: '-1px', marginRight: '10px' }}
                                />
                                <input type="invoiceFields" className='mutualFriendInput invoiceFields' placeholder="enter customer email" />
                            </span>
                            <span className="alignEnd" style={{ float: 'right', marginRight: '-8px' }}>
                                <img src={invoiceLink} style={{ marginTop: '0px' }} className='floatRight' alt="walletGreaterSign" />
                            </span>
                        </div>

                        <div className="resolutionOptionstoggle optionChange autoPickedData invoiceFeilds">
                            <span className="alignStart">
                                <img
                                    src={invoiceWallet}
                                    alt="mapIcon"
                                    style={{ marginTop: '-1px', marginRight: '10px', marginLeft: '2px' }}
                                />
                                <input type="invoiceFields" className='mutualFriendInput invoiceFields' placeholder="enter customer wallet" style={{ width: '65%' }} />
                            </span>
                            <span className="alignEnd" style={{ float: 'right', marginRight: '-8px' }}>
                                <img src={invoiceLink} style={{ marginTop: '0px' }} className='floatRight' alt="walletGreaterSign" />
                            </span>
                        </div>

                        <div className="row">
                            <div className="col-6">
                                <div className="resolutionOptionstoggle optionChange autoPickedData invoiceFeilds" style={{ width: '107%' }} onClick={() => {
                                    this.setState({ handleAddInvoice: false })
                                    if (this.state.paymentCalender === true) {
                                        this.setState({ paymentCalender: false })
                                    } else {
                                        this.setState({ paymentCalender: true })
                                    }
                                }}>
                                    <span className="alignStart">
                                        <img
                                            src={invoiceCalender}
                                            alt="mapIcon"
                                            style={{ marginTop: '-1px', marginRight: '10px', marginLeft: '2px' }}
                                        />
                                        <b style={{ position: 'relative', top: '1px' }}>{formatedCalenderValueUI}</b>
                                    </span>
                                    <span className="alignEnd" style={{ float: 'right', marginRight: '-8px' }}>
                                        <img src={invoicePaymentArrow} style={{ marginTop: '9px' }} className='floatRight' alt="walletGreaterSign" />
                                    </span>
                                </div>
                            </div>
                            <div className="col-6">
                                <div className="resolutionOptionstoggle optionChange autoPickedData invoiceFeilds" style={{ width: '107%', marginLeft: '-12px' }} onClick={() => {
                                    this.setState({ paymentCalender: false })
                                    if (this.state.handleAddInvoice === true) {
                                        this.setState({ handleAddInvoice: false })
                                    } else {
                                        this.setState({ handleAddInvoice: true })
                                    }
                                }}>
                                    {attachedInvoice}
                                </div>
                            </div>
                        </div>
                        {paymentCalender}
                        {addInvoice}
                        {invoiceBtn}
                    </div>
                </div>
                <ToastContainer />
            </div >
        );
    }
}

export default App;